import { ViewChild, Component, Input, ElementRef, OnChanges } from '@angular/core';
import { News, NewsService } from '../news.service';
import { Utils } from '../../utils';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-news-detail',
  templateUrl: 'news-detail.component.html',
  styleUrls: ['news-detail.component.css'],
  host: {
    '(window:resize)': 'onResize($event)',
    '(window:scroll)': 'onScroll($event)'
  }
})
export class NewsDetailComponent {

  @Input() news: News;
  newsList: any[];
  nextNews: News;
  public isMobileMenu: boolean = false;
  public minimal: boolean = false;
  public id: string;
  public showSideDrawer: boolean = false;
  public sidedrawercontent: Comment[];
  private tempId: String = "";
  private firstTime: Boolean = true;
  private safeHtml: any = [];
  public newsImagePath: any[];
  public numberOfNewsimage: number;
  scrollEnd: number = 0;
  shareBlockHeight: number = 0;
  node: ElementRef;
  flySocial: any;
  windowHeight: number = 0;
  flySocialHeight: number = 0;
  isFlySocialFixed: boolean = false;
  loadingDone: boolean = false;
  twitEmbed: String[] = [];

  currentNodeIndex: number = 0;
  nextNodeIndex: number;
  previousNodeIndex: number;

  private mobilereolution: boolean = false;
  private desktopResolution: boolean = false;

  constructor(private _newsService: NewsService,
    private route: ActivatedRoute, el: ElementRef, private sanitizer: DomSanitizer) {
    this.node = el;
  }

  toggleSideDrawer(audio) {
    this.showSideDrawer = !this.showSideDrawer;
  }



  ngOnInit(): void {
    window.scroll(0, 0);
    this.safeHtml = [];

    if (this.node.nativeElement.clientWidth < 767) {
      this.isMobileMenu = true;
    }

    this.flySocial = this.node.nativeElement.querySelectorAll(".fly-social-block")[0];
    //console.log(this.flySocial);
    this.windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

    //this.shareBlockHeight =  this.flySocial.scrollHeight;
    //this.scrollEnd = this.node.nativeElement.querySelectorAll(".detail-list .content")[0].scrollHeight;
    //console.log(this.flySocial.scrollHeight);

    this.route.params.forEach((params: Params) => {
      this.id = params['id'];
      this._newsService.getNewsDetail(params['id']).subscribe(
        data => {
          this.news = data[0]
          var fragElem = document.createElement("div");

          this.newsImagePath = this.news.thumbnail.split(",");

          this.numberOfNewsimage = this.newsImagePath.length;
          fragElem.innerHTML = this.news.body.toString();

          let iframe = fragElem.querySelectorAll("iframe");

          let blockquote = fragElem.querySelectorAll("blockquote a");
          for (let i = 0; i < blockquote.length; i++) {
            var src = blockquote[i].getAttribute("href");
            src = src.substr(src.lastIndexOf("/") + 1, src.length);
            this.twitEmbed.push(src);
          }

          //console.info('iframe',fragElem.querySelectorAll("iframe")[0]);
          //conver the iframe dom to string
          for (let i = 0; i < iframe.length; i++) {
            let el = document.createElement("div");

            el.appendChild(iframe[i]);
            this.safeHtml.push(this.sanitizer.bypassSecurityTrustHtml(el.innerHTML));
          }

          this.safeHtml.push(this.news.body.toString());

          console.info("Safehtml"+this.safeHtml);
        },
        err => console.error(err),
        () => {
          console.log('done from get news deatil');

        }
      );

      this._newsService.getNewsIdList(params['id']).subscribe(
        data => {
          this.newsList = data;
          let i = 0;
          while (true) {
            if (this.newsList[i]) {
              console.info("This is node id" + Utils.getNID(this.id));
              if (this.newsList[i]["nid"]) {
                if (this.newsList[i]["nid"] == Utils.getNID(this.id)) {
                  console.info("line next" + this.newsList[i]["nid"]);
                  this.currentNodeIndex = i;
                  break;
                }
              }
              i++;
            } else {
              break;
            }
          }

          //console.log('-------------------newsList--------------------------');
          //console.log(this.newsList);
          //console.log(this.currentNodeIndex);
          //console.log(this.previousNodeIndex);

          if (this.currentNodeIndex == 0) {
            this.nextNodeIndex = this.currentNodeIndex + 1;
            this.previousNodeIndex = 0;
          } else if (!this.newsList[this.currentNodeIndex + 1]) {
            this.nextNodeIndex = 0;
            this.previousNodeIndex = this.currentNodeIndex - 1;
          } else {
            this.nextNodeIndex = this.currentNodeIndex + 1;
            this.previousNodeIndex = this.currentNodeIndex - 1;
          }

          if (this.nextNodeIndex != 0) {
            this.nextArticle();
          } else {
            this.nextNews = undefined;
          }
        },
        err => console.error(err),
        () => console.log('msgs', this.newsList)
      );
    });
    window.scroll(0, 0);
    //this.loadNewsVideo();
    this.tempId = this.id;
    this.firstTime = false;
    if (window.screen.width < 1023) {
      this.mobilereolution = true;
      this.desktopResolution = false;
    }
    else {
      this.desktopResolution = true;
      this.mobilereolution = false;
    }
  }
  ngOnChanges() {
    window.scroll(0, 0);
    if (!this.firstTime) {

    }
  }
  lightboxClose(closed: boolean) {
    if (closed) {
      this.safeHtml = [];
    }
  }
  loadNewsVideo() {
    this._newsService.getNewsIdList(this.id).subscribe(

      data => {
        this.news = data[0];
        var fragElem = document.createElement("div");
        fragElem.innerHTML = this.news.body.toString();
        let iframe = fragElem.querySelectorAll("iframe")[0];
        //console.info('iframe',fragElem.querySelectorAll("iframe")[0]);
        /* conver the iframe dom to string */
        let el = document.createElement("div");
        el.appendChild(iframe.cloneNode(false));

        this.safeHtml = this.sanitizer.bypassSecurityTrustHtml(el.innerHTML);

        //this.news.body = this.sanitizer.bypassSecurityTrustHtml(this.news.body);
        //console.log("this.safeHtml "+this.safeHtml);
      },
      err => console.error(err),
      () => console.log('done')
    );
  }
  nextArticle() {
    this._newsService.getMinifiedNewsDetail("test-" + this.newsList[this.nextNodeIndex]['nid']).subscribe(
      data => this.nextNews = data[0],
      err => console.error(err),
      () => console.log('done')
    );

  }

  onResize(event): void {
    if (event.target.innerWidth < 767) {
      this.isMobileMenu = true;
      this.scrollEnd = this.flySocial.offsetTop;
      this.flySocialHeight = this.flySocial.clientHeight;
    } else {
      this.isMobileMenu = false;
      this.isFlySocialFixed = false;
    }
    if (event.target.innerWidth < 1023) {
      this.mobilereolution = true;
      this.desktopResolution = false;
    }
    else {
      this.desktopResolution = true;
      this.mobilereolution = false;
    }
  }

  ngAfterViewChecked(): void {

    //alert(" alert"+this.myIdentifier.nativeElement.offsetWidth);
  }

  onScroll(event): void {
    if (this.isMobileMenu) {
      if (!this.loadingDone) {
        this.scrollEnd = this.flySocial.offsetTop;
        this.flySocialHeight = this.flySocial.clientHeight;
        this.isFlySocialFixed = true;
      }
      this.loadingDone = true;
    }
  }
}


