import { Component, Input, OnInit, Output, EventEmitter, OnChanges } from '@angular/core';
import { Http, Response } from '@angular/http';
import {Observable} from 'rxjs/Rx';
import { Injectable } from '@angular/core';
import { Utils } from '../utils'
import { environment } from '../../environments/environment';
import {Spotify,SpotifyService} from './spotify.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Router, ActivatedRoute, Params  } from '@angular/router';




@Component({
  selector: 'app-spotify',
  templateUrl: './spotify.component.html',
  styleUrls: ['./spotify.component.css'],
  providers: [SpotifyService]
})



export class SpotifyComponent implements OnInit, OnChanges {
  public spotify: Spotify[];
  public spotifyFeed: Spotify[];
  @Input() spotifyID: String;
  public SpotifyIframesrc: string;
  public spotifylength:number;
  @Input() artistname:string;
  @Output()
  notify: EventEmitter<number> = new EventEmitter<number>();

  constructor(private route: ActivatedRoute , private _spotifyService: SpotifyService,public sanitizer: DomSanitizer) { }

  ngOnInit() {
  
    this.spotify = this.getSpotify();
    
  }
 
   getSpotify(){
       
        this.spotify = [];
        
        this._spotifyService.getspotifyNew(this.artistname).subscribe(
            data => { 
                this.spotify = data;
               // this.spotifyFeed = this._spotifyService.mapSpotifyInfo(this.spotify);
               
            },
            err => console.error( err),
            () => {
                //this.spotifylength = this.spotifyFeed.length;
                this.spotifylength=this.spotify.length;
                this.notify.emit(this.spotifylength);
                console.log('done');}
        );
        return this.spotify;
    }

    ngOnChanges(): void {
      this.spotify = this.getSpotify();
            

        }

  


}





