import { Component, OnInit ,ViewChild,ElementRef} from '@angular/core';
import { Title } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/platform-browser';
import { PageScrollConfig, PageScrollService, PageScrollInstance , } from 'ng2-page-scroll';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Inject } from "@angular/core";
@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css'],
  host: {
    '(window:resize)': 'onResizefot($event)'
},
})
export class FooterComponent implements OnInit {
  @ViewChild('container')
  private container: ElementRef;
  private mobilereolution: boolean = false;
  private desktopResolution: boolean = false;
  constructor(private titleService: Title , private _router: Router, private pageScrollService: PageScrollService, @Inject(DOCUMENT) private document: any) { }

  ngOnInit() {
    if(window.screen.width < 1023){
      this.mobilereolution = true;
      this.desktopResolution = false;
  }
  if(window.screen.width > 1023){
      this.desktopResolution = true;
      this.mobilereolution = false;
  }
  }
  onResize(event){
    if(event.target.innerWidth < 1023){
      this.mobilereolution = true;
      this.desktopResolution = false;
  }
  if(event.target.innerWidth > 1023){
      this.desktopResolution = true;
      this.mobilereolution = false;
  } 
  }
    onResizefot(event) {
      if(event.target.innerWidth < 1023){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(event.target.innerWidth > 1023){
          this.desktopResolution = true;
          this.mobilereolution = false;
      } 
  }
public setTitle(newTitle: string) {
  
  
   
      this.titleService.setTitle(newTitle);
  
   
 }
 public scrolltop(){
  if(location.pathname == "/" ){
    // window.scroll(0,0)  ;
    PageScrollConfig.defaultScrollOffset=50;
    let pageScrollInstance: PageScrollInstance = PageScrollInstance.simpleInstance(this.document, '#head2');
    this.pageScrollService.start(pageScrollInstance);
   }
   else{
    let link = ['/'];
    this._router.navigate(link);
   }
 }
}

  
  
  
  
  



