import { Component, Input, ElementRef, OnInit, OnChanges, Inject, ViewChild } from '@angular/core';
import { Artist, ArtistService } from "../artists.services";
import { Utils } from '../../utils';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

import { News, NewsService } from '../../+news/news.service';
import { ArtistblockComponent } from '../artist-block/artist-block.component';

import { DOCUMENT } from '@angular/platform-browser';
import { HostListener } from "@angular/core";


@Component({
  selector: 'app-artist-detail',
  templateUrl: './artist-detail.component.html',
  styleUrls: ['./artist-detail.component.css'],

})
export class ArtistDetailComponent implements OnInit, OnChanges {

  @Input() artist: Artist;
  public artists: Artist[];
  public bitID: String = "data-artist-name";
  public isMobileMenu: boolean = false;
  node: ElementRef;
  flySocial: any;
  artistList: any[];
  public id: string;
  public readMoreFlag: boolean = true;
  public title: string;
  windowHeight: number = 0;
  private safeHtml: any = [];
  twitEmbed: String[] = [];
  private tempId: String = "";
  private firstTime: Boolean = true;
  private DropDownWrapper: Boolean = false;
  public nid: String;
  public stringTruncate: number = 200;
  public artistlength: number;
  public tourBitId: String = "";
  private myArtistValue: String;
  artistNameChange: boolean = true;
  private cache: number = 1;
  currentNodeIndex: number = 0;
  nextNodeIndex: number;
  previousNodeIndex: number;
  public SpotifyIframesrc: string;
  public SpotifyIframeURL: SafeResourceUrl;
  public artistBitID: boolean = true;
  public pagescrolloffset: number;
  public scrolloffset: number;
  public scaleoffset: number;
  public readMoreoffset: number;
  public nospotifyContent: boolean = true;
  public artistnid:String;
  constructor(private _artistService: ArtistService,
    private route: ActivatedRoute, el: ElementRef, private sanitizer: DomSanitizer, @Inject(DOCUMENT) private document: any) {

    this.node = el;

  }

  getArtists(artistnid:String): Artist[] {
    this.artists = [];

    this._artistService.getArtistswithfilter(artistnid).subscribe(
      data => {
        this.artists = data;
       
      },
      err => console.error(err),
      () => {
        this.artistlength = this.artists.length;


      }
    );
    return this.artists;
  }
  ngOnInit(): void {
    window.scroll(0, 0);
    

    if (this.node.nativeElement.clientWidth < 767) {
      this.isMobileMenu = true;
    }

    this.flySocial = this.node.nativeElement.querySelectorAll(".fly-social-block")[0];

    this.windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;


    this.route.params.forEach((params: Params) => {
      this.title = params['id'];

      //console.info("the param value" + this.title);
      this._artistService.getArtistDetail(params['id']).subscribe(

        data => {

          this.artist = data[0];
          var fragElem = document.createElement("div");
          fragElem.innerHTML = this.artist.body.toString();
          this.safeHtml.pop();
          this.safeHtml.push(this.artist.body.toString());
          this.artistNameChange = true;
          this.myArtistValue = this.artist.title;
          this.DropDownWrapper = false;
          this.nospotifyContent=true;


        },
        err => console.error(err),
        () => {
          console.log('done');
          this.artistnid = this.artist.nid;
          this.getArtists(this.artistnid);  
          this.stringTruncate = 200;
          if (this.artist.body.length > this.stringTruncate) {
            this.readMoreFlag = true;

          }
          else {
            this.readMoreFlag = false;
          }
          if (this.artist.field_artist_bit_id == "") {
            this.artistBitID = false;
          }
          else {
            this.artistBitID = true;
          }
          if (this.artistNameChange) {
            this.myArtistValue = "";
            setTimeout(() => {
              this.myArtistValue = this.artist.title;
              
            }, 1000);

          }
          this.SpotifyIframesrc = "https://open.spotify.com/follow/1/?uri=spotify:artist:" + this.artist.field_artist_spotify_id + "&size=basic&theme=light&show-count=0";
          this.SpotifyIframeURL = this.sanitizer.bypassSecurityTrustResourceUrl(this.SpotifyIframesrc);
        }
      );

    });
    window.scroll(0, 0);
    //this.loadNewsVideo();
    this.tempId = this.id;
    
    //this.firstTime = false;
  }

  ngOnChanges(): void {



  }

  // iframeURL(spotifyID:string){
  //     this.SpotifyIframesrc = "https://open.spotify.com/follow/1/?uri=spotify:artist:" + spotifyID + "&size=basic&theme=light&show-count=0";
  //     this.SpotifyIframeURL = this.sanitizer.bypassSecurityTrustResourceUrl(this.SpotifyIframesrc);
  //     return this.SpotifyIframeURL;
  // }
  checkSpotifyLength(spotifyCount: number): void {
    if (spotifyCount == 0) {
      this.nospotifyContent = false;
    
    }
    else{
      this.nospotifyContent=true;
    }
  }
  readMore() {
    this.stringTruncate = this.artist.body.length;
    this.readMoreFlag = false;
    this.readMoreoffset = this.calculatesroll();
  }
  readLess() {

    this.stringTruncate = 200;
    this.readMoreFlag = true;
    window.scroll(0, this.readMoreoffset);


  }
  calculatesroll(): number {
    let scrollTop = document.body.scrollTop;
    return scrollTop;
  }



  @HostListener("window:scroll", ['$event'])
  updateOnScroll(event) {
    let scrollTop = event.pageY | document.body.scrollTop;
    this.pagescrolloffset = scrollTop;

    if (this.pagescrolloffset < 660) {
      this.scrolloffset = this.pagescrolloffset;
      this.scaleoffset = this.scrolloffset
    }
    if (this.pagescrolloffset > 200) {
      this.scaleoffset = 300;
    }
  }
  detailbannerStyle(): any {
    return {
      'left': '0px', 'top': '0px', 'overflow': 'hidden', 'height': 'calc(100vh - 94px)', 'margin': '0px', 'padding': '0px', 'width': '100%', 'z-index': '-999998', 'position': 'absolute',
      'opacity': (1 - ((this.scrolloffset + 300) / 1000)),
      '-moz-transform': 'translate3d(0px, ' + (this.scaleoffset / 3.5) + 'px, 0px) scale(' + (1 + (this.scaleoffset / 730)) + ',' + (1 + (this.scaleoffset / 730)) + ' )',
      'transform': 'translate3d(0px, ' + (this.scaleoffset / 3.5) + 'px, 0px) scale(' + (1 + (this.scaleoffset / 730)) + ',' + (1 + (this.scaleoffset / 730)) + ' )',
      '-webkit-transform': 'translate3d(0px, ' + (this.scaleoffset / 3.5) + 'px, 0px) scale(' + (1 + (this.scaleoffset / 730)) + ',' + (1 + (this.scaleoffset / 730)) + ' )',
      '-ms-transform': 'translate3d(0px, ' + (this.scaleoffset / 3.5) + 'px, 0px) scale(' + (1 + (this.scaleoffset / 730)) + ',' + (1 + (this.scaleoffset / 730)) + ' )'
    };
  }




}



