import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterByType'
})
export class FilterByType implements PipeTransform {
  transform(value: any, args?: any): any {
   
    console.log(args);
    let filter = args.toLocaleLowerCase();
    if(args!="All"){
      // return filter ? value.filter(t=> t.field_type.toLocaleLowerCase().indexOf(filter) != -1) : value;
      if (filter) {
        value = value.filter(t=> t.field_type.toLocaleLowerCase().indexOf(filter) != -1);
      }
      if (value.length > 0) {
        return value;
      }
    }
    else{
       return value;
    }
   
  }
}



